import type from './type'

export default class data {
    static getCareer2(id) {
        let text;
        type.role.forEach((value) => {
            if (id === value.id) {
                text = value.label;
            }
        });
        return text;
    }

    static getCareer(id) {
        for (let i = 0; i < type.role.length; i++) {
            if (id === type.role[i].id) {
                return type.role[i].label;
            }
        }
    }

    static getOccupation(id) {
        let value1 = parseInt(id.substring(0, 3));
        let value2 = parseInt(id);
        for (let i = 0; i < type.industry.length; i++) {
            if (value1 === type.industry[i].value) {
                for (let j = 0; j < type.industry[i].children.length; j++) {
                    if (value2 === type.industry[i].children[j].value) {
                        return type.industry[i].label + '/' + type.industry[i].children[j].label;
                    }
                }
            }

        }
    }
}
