<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .header {
        .input {
            min-width: 150px;
            max-width: 70%;
        }
        .header-col {
            height: 60px;
        }
        .header-col-select {
            width: 80%;
        }
    }

    .input {
        min-width: 150px;
        max-width: 70%;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }

    .num-input {
        width: 100px;
    }

</style>

<template>
    <div class="page">
        <el-table
                :data="data.data"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="user_mobile"
                    label="手机">
            </el-table-column>
            <el-table-column
                    prop="registertime"
                    label="申请时间">
            </el-table-column>
            <el-table-column
                    prop="username"
                    label="用户名">
            </el-table-column>
            <el-table-column
                    prop="email"
                    label="邮箱">
            </el-table-column>
            <el-table-column
                    prop="company"
                    label="公司">
            </el-table-column>
            <el-table-column
                    prop="occupation"
                    label="行业">
                <template slot-scope="scope">
                    <span>{{getOccupation(scope.row.occupation)}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="career"
                    label="职业">
                <template slot-scope="scope">
                    <span>{{getCareer(scope.row.career)}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="status"
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.status===1?'已通过':'未审核'}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button v-show="scope.row.status===0" @click="access(scope.row,1)" type="text" size="small">通过审核
                    </el-button>
                    <el-button v-show="scope.row.status===1" @click="access(scope.row,0)" type="text" size="small">重置审核
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :page-count="data.last_page"
                    @current-change="loadData"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import data from '../../js/utils/data'
    import api from '../../js/api/activity'

    export default {
        data() {
            return {
                data: {},
            }
        },
        mounted() {
            this.loadData();
        },
        methods: {
            getCareer(id) {
             return data.getCareer(id)
            },
            getOccupation(id){
                return  data.getOccupation(id)
            },
            loadData() {
                api.applyList({id: this.$route.query.id}, res => {
                    this.data = res.data.data;
                });
            },
            access(item, status) {
                let data = {id: item.id, status: status};
                api.check(data, () => {
                    this.$message.success('操作成功');
                    item.status = status;
                })
            },

        }
    }
</script>

